import React, { useState } from "react";
const Korissa = ({tuote,i})=>{
    return(
        <div className="center" key={i}>
            <div className="tuote">
                <div className="kuvadiv">
                    <img src={tuote.img} className="tuotekuva"/>
                </div>
                <div className="tietodiv">
                    <p><b>{tuote.name}</b></p>
                    <p>Korissa: {tuote.cart}</p>
                    <p>Yhteensä: {Math.round((tuote.cart*tuote.hinta)*100)/100}€</p>
                </div>
            </div>
        </div>
    )
}
const Kassa = ({tuotteetdata, setProducts, tuotteetalku, onClick2, setShowText2}) =>{

    const [etu, setetu] = useState("");
    const [suku, setsuku] = useState("");
    const [mail, setmail] = useState("");
    const [puh, setpuh] = useState("");

    const [katu, setkatu] = useState("");
    const [paikka, setpaikka] = useState("");
    const [numero, setnumero] = useState("");

    const [kuponki, setkuponki] = useState("");

    const [asiakas, setasiakas] = useState({etunimi:"" ,sukunimi:"" ,mail:"",puhelin: "", katuosoite: "", postitoimipaikka:"", postinumero: ""})

    const resetoi = () =>{
        setProducts(tuotteetalku);
        setShowText2(true);
    }

    const [alennus, setalennus] = useState(Math.round((tuotteetdata.map(tuote => tuote.cart * tuote.hinta).reduce((a, b) => a + b))*100)/100);
    
    const submit = (e) =>{
        e.preventDefault();
        setasiakas({etunimi: etu,sukunimi: suku,mail:mail,puhelin: puh, katuosoite: katu, postitoimipaikka:paikka, postinumero: numero});

        if(kuponki == "free"){
            setalennus(0);
        }else if(kuponki == "lol"){
            setalennus(alennus * 0.9);
        }else if(alennus>=200){
            setalennus(alennus * 0.925);
        }else if(alennus>=100){
            setalennus(alennus * 0.95);
        }

        setetu("");
        setsuku("");
        setmail("");
        setpuh("");

        setkatu("");
        setpaikka("");
        setnumero("");

        setkuponki("");
    }

    return(
        <div className="center">
            {asiakas.etunimi.length<1 ?
                <div>
                    <h1>Tilaus</h1>
                    <div className="center">
                        {tuotteetdata.filter(tuote => tuote.cart > 0).map((f) => <Korissa key={f.id} tuote={f}/>)}
                    </div>
                    <br/>
                    <div>
                        <p>Yhteensä: {Math.round((tuotteetdata.map(tuote => tuote.cart * tuote.hinta).reduce((a, b) => a + b))*100)/100}€</p>
                        <hr/>
                        <div className="center">
                            <div className="lomake">
                                <h2>Tilaus lomake</h2>
                            <form onSubmit={submit}>
                                <div className="lomakkeet">
                                    <div className="inputdiv">
                                        <h3>Henkilökohtaiset tiedot</h3>

                                        <label>Etunimi*</label><br/>
                                        <input type="text" onChange={event => setetu(event.target.value)} required/><br/>

                                        <label>Sukunimi*</label><br/>
                                        <input type="text" onChange={event => setsuku(event.target.value)} required/><br/>

                                        <label>Sähköposti*</label><br/>
                                        <input type="mail" onChange={event => setmail(event.target.value)} required/><br/>

                                        <label>Puhelinnumero*</label><br/>
                                        <input type="tel" onChange={event => setpuh(event.target.value)} required/><br/>
                                    </div>
                                    <div className="inputdiv">
                                        <h3>Osoite tiedot</h3>

                                        <label>Katuosoite*</label><br/>
                                        <input type="text" onChange={event => setkatu(event.target.value)} required/><br/>

                                        <label>Postitoimipaikka*</label><br/>
                                        <input type="text" onChange={event => setpaikka(event.target.value)} required/><br/>

                                        <label>Postinumero*</label><br/>
                                        <input type="text" onChange={event => setnumero(event.target.value)} required/><br/>
                                    </div>
                                </div>
                                <label>Kuponki: </label>
                                <p className="kuponki"><i>Jos yhteishinta on 100€ tai yli saat 5% alennusta,<br/>mutta jos hinta on yli 200€ saat 7.5% alennusta!<br/>Kupongit antavat enemmän alennusta aina!</i></p>
                                <input className={(kuponki=="free" || kuponki=="lol")?"kupkyl":"kupei"} type="ale" onChange={event => setkuponki(event.target.value)} /><br/><br/>
                                <input className="vihrea" type="submit" value="Tilaa"/>
                            </form>
                            </div>
                        </div>
                    </div>
                    <button className="punainen" onClick={onClick2}>Takaisin</button>
                </div>
            :
                <div className="center">
                    <div className="tuote">
                        <div className="tietodiv">
                            <h1>Tilaus:</h1>
                            {tuotteetdata.filter(tuote => tuote.cart > 0).map((tuote) => <p><b>{tuote.name}</b> x {tuote.cart}</p>)}
                            <p><b>Lopullinen hinta:</b> {Math.round(alennus*100)/100}€</p>
                            <p><b>Tilaaja:</b> {asiakas.etunimi} {asiakas.sukunimi}</p>
                            <p><b>Sähköposti:</b> {asiakas.mail}</p>
                            <p><b>Puhelinnumero:</b> {asiakas.puhelin}</p>
                            <p><b>Osoite:</b> {asiakas.katuosoite} {asiakas.postitoimipaikka} {asiakas.postinumero}</p>
                            <p><b>Kiitos tilauksestasi!</b></p>
                        </div>
                        <button className="punainen" onClick={resetoi}>Etusivulle</button>
                    </div>
                </div>
            }
        </div>
    )
}

export {Kassa};