import React, { useState } from "react";
const Korissa = ({tuote})=>{
    return(
        <div className="center">
            <div className="tuote">
                <div className="kuvadiv">
                    <img src={tuote.img} className="tuotekuva"/>
                </div>
                <div className="tietodiv">
                    <p><b>{tuote.name}</b></p>
                    <p>Korissa: {tuote.cart}</p>
                    <p>Yhteensä: {Math.round((tuote.cart*tuote.hinta)*100)/100}€</p>
                </div>
            </div>
        </div>
    )
}
const Ostoskori = ({tuotteetdata, setProducts, tuotteetalku, onClick2}) =>{
    const tyhjenna = () =>{
        setProducts(tuotteetalku);
    }
    if(!tuotteetdata.map(tuote => tuote.cart).reduce((a, b) => a + b) < 1){
        return(
            <div className="center">
                <div>
                    <div className="center">
                        {tuotteetdata.filter(tuote => tuote.cart > 0).map((f) => <Korissa key={f.id} tuote={f}/>)}
                    </div>
                    <br/>
                    <div>
                        <p>Yhteensä: {Math.round((tuotteetdata.map(tuote => tuote.cart * tuote.hinta).reduce((a, b) => a + b))*100)/100}€</p>
                        <button className="vihrea" onClick={onClick2}>Kassalle</button>
                        <button className="punainen" onClick={tyhjenna}>Tyhjennä</button>
                    </div>
                </div>
            </div>
        )
    }
    return(
        <div className="center">
            <h2><b>Ostoskorisi on tyhjä!</b></h2>
        </div>
    )
}

export {Ostoskori};