import React, { useState } from "react";

const Tuote = ({tuote, addtocart, removefromcart}) =>{
    const [showText1, setShowText1] = useState(false);
    const onClick1 = () => setShowText1(!showText1);
    return(
        <div className="center">
            <div className="tuote">
                <div className="kuvadiv">
                    <img src={tuote.img} className="tuotekuva"/>
                </div>
                <div className="tietodiv">
                    <p><b>{tuote.name}</b></p>
                    <p><i>{tuote.hinta}</i>€/kpl</p>
                    <p onClick={onClick1} className="lisatieto">Lisätiedot</p>
                    {showText1 ? 
                    <div>
                        <hr/>
                        <p>{tuote.tietoja}</p>
                    </div>:null}
                </div>
                <div className="tietodiv">
                    <button className="vihrea" onClick={()=>addtocart(tuote.id)}>Lisää koriin</button>
                    {tuote.cart!=0 &&
                    <div>
                        <button className="punainen" onClick={()=>removefromcart(tuote.id)}>Poista korista</button>
                        <p>Korissa: {tuote.cart} | Yhteensä: {Math.round((tuote.cart*tuote.hinta)*100)/100}€</p>
                    </div>}
                </div>
            </div>
        </div>
    )
}

const Tuotteet = ({tuotteetdata, setProducts}) =>{
    const addtocart = id => {
        setProducts(tuotteetdata.map(tuote => {
            if(id===tuote.id){
                const tempProduct = {...tuote, cart: tuote.cart + 1}
                return tempProduct;
            }else{
                return tuote
            }
        }))
    }
    const removefromcart = id => {
        setProducts(tuotteetdata.map(tuote => {
            if(id===tuote.id){
                const tempProduct = {...tuote, cart: tuote.cart - 1}
                return tempProduct;
            }else{
                return tuote
            }
        }))
    }
    return(
        <div className="center">
            {tuotteetdata.map((f,i) => <Tuote key={i} tuote={f} addtocart={addtocart} removefromcart={removefromcart} setProducts={setProducts}/>)}
        </div>
    )
}

export {Tuote,Tuotteet};