//import osio
import logo from './logo.svg';
import React, { useState } from "react";
import './App.css';
import {Tuotteet} from './components/Tuotteet';
import {Ostoskori} from './components/Ostoskori';
import {Kassa} from './components/Kassa';

const App = () => { //function App()
  const tuote1 = {id: 1,name: "Special edition muki", hinta: "12.99", tietoja: "Tämä hieno täysin valkoinen muki on täydellinen.", img:"https://freesvg.org/img/1554826850.png", cart: 0};
  const tuote2 = {id: 2,name: "Liian iso liipis", hinta: "9.99", tietoja: "Tämä lippis on liian iso kenellekkään.", img:"https://freesvg.org/img/baseballcap.png", cart: 0};
  const tuote3 = {id: 3,name: "T-paita", hinta: "15", tietoja: "Tämä paita on 51% muovia, 9% puuvillaa ja 40% polyesteriä", img:"https://freesvg.org/img/t-shirt_white.png", cart: 0};
  const tuote4 = {id: 4,name: "Valkoinen juliste", hinta: "0.01", tietoja: "Täysin valkoinen juliste...", img:"https://cdn.pixabay.com/photo/2021/03/27/10/20/frame-6127989_1280.jpg", cart: 0};
  const tuotteetalku = [tuote1, tuote2, tuote3, tuote4];
  const [tuotteet, setProducts] = useState(tuotteetalku);

  //joo
  const [showText2, setShowText2] = useState(true);
  const onClick2 = () => setShowText2(!showText2);
  

  return (
    <div className="App">
      <header className="App-header">
        Turha fanikauppa
      </header>
      {showText2 ?
      <div>
      <h1>Tuotteet</h1>
      <Tuotteet tuotteetdata={tuotteet} setProducts={setProducts}/>
      <hr/>
      <h1>Ostoskori</h1>
      
      <Ostoskori tuotteetdata={tuotteet} setProducts={setProducts} tuotteetalku={tuotteetalku} onClick2={onClick2}/>
      </div>
      :<div>
        <Kassa tuotteetdata={tuotteet} setProducts={setProducts} tuotteetalku={tuotteetalku} onClick2={onClick2} setShowText2={setShowText2}/>
      </div>}
    </div>
  );
}

export default App;
